@font-face {
    font-family: 'SuperSmash';
    src: local('SuperSmash'), url('./fonts/super_smash.ttf') format('truetype');
}

@font-face {
    font-family: 'Anton';
    src: local('Anton'), url('./fonts/Anton-Regular.ttf') format('truetype');
}

@font-face {
    font-family: 'SSFIV';
    src: local('SSFIV'), url('./fonts/SSF4.ttf') format('truetype');
}


body {
    margin: 0;
    font-family: 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-y: auto;
    background-image: url('otherPictures/SmashUltimate.jpg');
    background-size: cover;
    padding-bottom: 30px;
}

.bobeffect {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
}

.bobeffect:hover, .bobeffect:focus, .bobeffect:active {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    -webkit-transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
    transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
}


.resetBtn {

    background-color: darkgrey !important;

}

.basicBtn{
    height: 50px !important;
    width: 150px !important;
    font-size: 20px !important;
    margin: 10px !important;
}

.textAligncenter{
    text-align: center;
}

.winBtn{
    background-color: mediumseagreen !important;
    width: 100%;
}

.loseBtn{
    background-color: indianred !important;
}

.MuiButtonBase-root:disabled {
    cursor: not-allowed;
    pointer-events: auto;
}

.iconsize {
    height: 100px;
    width: 100px;
    margin: 10px;
}

.bigTitle {
    font-size: 60px;
    margin: auto;
    font-family: 'Segoe UI','Roboto', serif;
}

.winnerTitle {
    font-size: 40px;
    margin: auto;
    font-family: 'Segoe UI','Roboto', serif;
}

.ssf4{
    font-family:"SSFIV"
}

.customTitle {
    text-align: center;
    margin-top: 50px;
    font-family: 'SuperSmash', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', serif;
}

.floatTitleLeftBest {
    float: left;
    background-color: coral !important;
    margin-right: 20px !important;
}

.floatTitleLeft {
    float: left;
    background-color: deepskyblue !important;
    margin-left: 50px;
    margin-right: 20px;
    margin-bottom: 50px;
}

.floatTitleRight {

    background-color: darkcyan !important;
    margin-left: 50px;
    margin-bottom: 50px;

    width: 250px;
    font-size: 30px;
}

.topContainer {
    text-align: center;
    padding: 10px;
}

.toggleSolo{
    color:mediumseagreen;
}

.smalldevice{
    font-size:40px;
    font-family:"SSFIV";
}

.fullwidthcontent{
    width: 100%;
}

.MuiButton-textPrimary {
    color: green;
}

.eventBonusMalus{

    text-transform: uppercase;
    margin:0
}

.eventGoal{
    margin-bottom: 10px;
}

.bonusTxt{
    color:darkgreen;
    text-align: center;

}

.malusTxt{
    color:red;
    text-align: left;
    padding-left: 50px;
}
.EventPlayer{
    color: mediumpurple;
    text-transform: capitalize;
}

.btnfullWidth{
    width: 100%;
}

.p1bg{
    background-color: darkblue !important;
    margin: 10px 0 !important;
}

.p2bg{
    background-color: darkred !important;
    margin: 10px 0 !important;
}

.flagPosition{
    float: left;
}

.activeFlag{
    filter: grayscale(0);
}

.unactiveFlag{
    filter: grayscale(1);
}

.flagPosition img{
    margin: 0 10px;
}


.ruleimg{
    position: absolute;
    top: 50%;  /* position the top  edge of the element at the middle of the parent */
    left: 50%; /* position the left edge of the element at the middle of the parent */
    width: 100%;
    height: 100%;
    transform: translate(-50%, -50%); /* This is a shorthand of
                                         translateX(-50%) and translateY(-50%) */

}

.autoimg{
    max-width: 100%;
    height: auto;
}

.ruleModal{
    background-image:url("./otherPictures/rule_bg.png");
    background-size: 80%;
    background-repeat: no-repeat;
    background-position:center;
    border-radius: 10px;
}

.winningModal{
    background-image:url("./otherPictures/winning_screen.png");
    background-size: 100%;
    background-repeat: no-repeat;
    background-position:center;
    min-height: 400px;
    border-radius: 10px;

}

.winningTop{
    margin-top: 25%;

}

.winningstyle{
    background-color: rgba(255, 255, 255, .5);
    border-radius: 5px;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;

}


@media (max-width: 1281px) {


}

@media (max-width: 600px) {

    .topmobile{
        padding-top: 180px;
    }

    .flagPosition {
        float: left;
        width:100%;
        padding-top: 10px;
    }

    .topContainer {
        height: 150px;
    }
    .customTitle {
        margin-top: 10px !important;
        margin-bottom: 10px !important;
    }

    .floatTitleLeftBest {
        width: 100%;
        background-color: coral !important;
        margin-right: 20px !important;
    }

    .floatTitleLeft {
        width: 100%;
        margin-top: 10px !important;
    }

    .floatTitleRight {
        margin-top: 10px !important;
        margin-bottom: 10px !important;
        width: 100%;
        background-color: darkcyan !important;

    }
    .toggleSolo{
        color:black !important;
    }

    .smalldevice{
        font-size: 20px;
    }

}
